var Column	= require('./Column'),
	Text	= require('./type/Text'),
	Row		= require('../data/Row'),
	Cell	= require('../data/Cell');

/**
 * ColumnConfiguration represents the configuration of all columns within
 * one Sheet. The ColumnConfiguration handles the mapping of object
 * properties to certain columns,, labeling, type definition and also
 * display information like width and alignment.
 *
 * @param {Array} aColumnConfig -  An array of Column definitions. {@see Column} for further details
 * @param {Styles} oStyles - Style collection that is used to register Cell or row specific styles
 * @module binding/ColumnBinding
 * @constructor
 */
function ColumnConfiguration(aColumnConfig, oStyles) {
	var column, i;

	this.columns = [];
	this.row = null;
	this.styles = oStyles;

	for (i = 0; i < aColumnConfig.length; i++) {
		column = aColumnConfig[i] instanceof Column ? aColumnConfig[i] : new Column(aColumnConfig[i], this.styles);
		this.columns.push(column);
	}
}

/**
 * Initializes the objects that are necessary for the binding. This
 * components are:
 * - a template Row
 * - a Cell for each Column
 * - a Style for each data type referenced by each Column
 *
 * @private
 */
ColumnConfiguration.prototype._initBinding = function() {
	var i;

	this.row = new Row();

	for (i = 0; i < this.columns.length; i++) {
		this.row.addCell(this.columns[i].getCell());
	}
};


/**
 * Creates an new Row and binds the provided data to the Row Cells according to the current ColumnConfiguration.
 *
 * @param {Object} oData - The data object that has to be bound to the row
 * @returns {Row} - Returns the created Row that contains the provided data
 */
ColumnConfiguration.prototype.bind = function(oData) {
	var i;

	if (this.row === null) {
		this._initBinding();
	}

	for (i = 0; i < this.columns.length; i++) {
		this.columns[i].bind(oData);
	}

	return this.row;
};


/**
 * Creates the header Row according to the current ColumnConfiguration and returns it.
 *
 * @returns {Row} - Header row according to the ColumnConfiguration
 */
ColumnConfiguration.prototype.getHeader = function() {
	var cell, row, type;

	row = new Row();
	type = new Text({
		font: {
			name: 'Arial',
			size: 11,
			bold: true
		},
		fill: {
			color: 'F7F7F7'
		}
	});

	/* Register header style */
	type.registerAt(this.styles);

	row.setStyle(type.getStyleId());
	row.addAttribute({
		name: 'customFormat',
		value: '1'
	});

	this.columns.forEach(function(column, index, array) {

		/* Create new Cell and assign value */
		cell = new Cell(type);
		cell.setValue(column.label);

		row.addCell(cell);
	});

	return row;
};


/**
 * Returns the size of the ColumnConfiguration - respectively the
 * count of the configured columns.
 *
 * @returns {number} Count of configured columns
 */
ColumnConfiguration.prototype.size = function() {
	return this.columns.length;
};

module.exports = ColumnConfiguration;