var Type = require('./Type');

/**
 * The Text class represents textual information without any
 * particular formatting. It provides font and sizing details that
 * can be used by the Styles class.
 *
 * @param {Object} [oConfig] - TODO
 * @constructor
 */
function Text(oConfig) {
	Type.call(this);

	this.font = oConfig ? oConfig.font : null;
	this.fill = oConfig ? oConfig.fill : null;
}

Text.prototype = Object.create(Type.prototype);
Text.prototype.constructor = Text;

Text.DEFAULT = {
	font: {
		size: 11,
		name: 'Arial'
	}
};

Text.prototype._createConfiguration = function() {
	return (this.font || this.fill) ? { font: this.font, fill: this.fill } : Text.DEFAULT;
};

module.exports = Text;