/**
 * The Type class defines a supported data type, like strings,
 * numeric values, decimals, dates, currencies and so on. It is NOT
 * part of the XML representation of the generated Excel document.
 *
 * This class is marked as abstract therefore only its sub-classes
 * should be used.
 *
 * @constructor
 * @abstract
 */
function Type() {
	this.styleId = null;
}

/**
 * Creates the type specific configuration that is used to register
 * its corresponding style in the Styles class.
 *
 * @abstract
 * @private
 */
Type.prototype._createConfiguration = function() {
	throw new Error('Abstract method must be implemented by its subclass!');
};

/**
 * Returns the assigned ID of the Type specific Style that is
 * registered in the Styles class.
 *
 * @returns {number} - ID of the registered Style
 */
Type.prototype.getStyleId = function() {
	if (this.styleId === null) {
		this.styleId = this.styles.addStyle(this._createConfiguration());
	}

	return this.styleId;
};

/**
 * Registers the Style configuration of this Type in the given Styles
 * class.
 *
 * @param {Styles} oStyles - Styles instance where the Type specific styling will be registered
 */
Type.prototype.registerAt = function(oStyles) {
	this.styles = oStyles;
};

module.exports = Type;