var XMLNode = require('../xml/XMLNode');

/**
 * This class represents the workbook.xml file within the generated
 * Office Open XML Spreadsheet. The workbook is the central element
 * of the Office Open XML Spreadsheet that references all the content.
 *
 * @constructor
 */
function Workbook() {
	XMLNode.call(this, Workbook.metadata);

	// Adds a default sheet so there is at least on sheet present
	this.addSheet('SAP Document Export');
}

Workbook.prototype = Object.create(XMLNode.prototype);
Workbook.prototype.constructor = Workbook;

Workbook.metadata = {
	tag: 'workbook',
	root: true,
	attributes: [
		{
			name: 'xmlns',
			value: 'http://schemas.openxmlformats.org/spreadsheetml/2006/main'
		},
		{
			name: 'xmlns',
			suffix: 'r',
			value: 'http://schemas.openxmlformats.org/officeDocument/2006/relationships'
		}
	],
	properties: [
		{
			tag: 'bookViews',
			properties: [
				{
					tag: 'workbookView',
					attributes: [
						{
							name: 'xWindow',
							value: '0'
						},
						{
							name: 'yWindow',
							value: '0'
						},
						{
							name: 'windowWidth',
							value: '22260'
						},
						{
							name: 'windowHeight',
							value: '12645'
						}
					]
				}
			]
		},
		{
			id: 'sheets',
			tag: 'sheets'
		}
	]
};

Workbook.prototype.addSheet = function(sName) {
	var count = this.sheets.childs.length + 1;
	var sheet = new XMLNode({
		tag: 'sheet',
		attributes: [
			{
				name: 'name',
				value: sName
			},
			{
				name: 'sheetId',
				value: '' + count
			},
			{
				name: 'id',
				prefix: 'r',
				value: 'rId' + count
			}
		]
	});

	this.sheets.addChild(sheet);
};

module.exports = Workbook;