var XMLNode = require('../xml/XMLNode');
var Alignment = require('./Alignment');

/**
 * Represents a particular Style within the Office Open XML Spreadsheet.
 * Instances of this type will be created by the Styles Class after
 * registering a new Style.
 *
 * @constructor
 */
function Style() {
	XMLNode.call(this, Style.metadata);
}

Style.prototype = Object.create(XMLNode.prototype);
Style.prototype.constructor = Style;

Style.metadata = {
	tag: 'xf',
	attributes: [
		{
			id: 'numberFormat',
			name: 'numFmtId',
			value: '0'
		},
		{
			id: 'font',
			name: 'fontId',
			value: '0'
		},
		{
			id: 'fill',
			name: 'fillId',
			value: '0'
		},
		{
			id: 'border',
			name: 'borderId',
			value: '0'
		}
	]
};

Style.DEFAULT = {
	font: {
		name: 'Arial',
		size: 11,
		family: '2',
		color: '1',
		bold: false
	},
	fill: {
		type: 'none'
	}
};

Style.prototype.getAlignment = function() {
	var alignProperty = 'alignment';

	return alignProperty in this ? this[alignProperty][alignProperty].value : null;
};

Style.prototype.getFontId = function() {
	return this.font.value;
};

Style.prototype.getFillId = function() {
	return this.fill.value;
};

Style.prototype.getBorderId = function() {
	return this.border.value;
};

Style.prototype.getNumberFormatId = function() {
	return this.numberFormat.value;
};

/**
 * Applies the alignment to the Style. The alignment must match the
 * predefined options of <code>Alignment</code> as far as it is not
 * null. Other values will be ignored.
 *
 * The value <code>null</code> will delete the current alignment.
 *
 * @param {string|null} sAlign - Alignment that gets applied or null
 */
Style.prototype.setAlignment = function(sAlign) {
	var alignProperty = 'alignment';

	this._apply(sAlign, 'Alignment');

	if (sAlign === null) {
		this.remove(alignProperty);
	}

	if (!Alignment.horizontal.contains(sAlign)) {
		return;
	}

	if (!(alignProperty in this)) {
		this.addChild({
			id: alignProperty,
			tag: 'alignment', // This tag is fix, so we can't use the alignProperty variable in this case
			attributes: [
				{
					id: alignProperty,
					name: 'horizontal'
				}
			]
		});
	}

	/* The property variable is used on the child node as well */
	this[alignProperty][alignProperty].value = sAlign;
};

Style.prototype.setFontId = function(nFontId) {
	this._apply(nFontId, 'Font');

	this.font.value = nFontId;
};

Style.prototype.setFillId = function(nFillId) {
	this._apply(nFillId, 'Fill');

	this.fill.value = nFillId;
};

Style.prototype.setBorderId = function(sBorderId) {
	this.border.value = sBorderId;
};

Style.prototype.setNumberFormatId = function(nNumFmtId) {
	this._apply(nNumFmtId, 'NumberFormat');

	this.numberFormat.value = nNumFmtId;
};

Style.prototype._apply = function(nId, sType) {
	var applyProperty = 'apply' + sType;

	this.remove(applyProperty);

	if (nId) {
		this.addAttribute({
			id: applyProperty,
			name: applyProperty,
			value: '1'
		});
	}
};


Style.prototype.equals = function(oStyle) {
	if (!(oStyle instanceof Style)) {
		return false;
	}

	return this.getFontId() === oStyle.getFontId()
		&& this.getFillId() === oStyle.getFillId()
		&& this.getBorderId() === oStyle.getBorderId()
		&& this.getNumberFormatId() === oStyle.getNumberFormatId();
};

module.exports = Style;